            >
          </v-col>
        </v-row>
        <v-row>
          <v-col md="2">
            <label>  <span>*</span></label>
          </v-col>
          <v-col md="4">
            <v-text-field
              v-model="newLandinfoarea.map_sheet_no"
              :rules="[required('Map Sheet No.')]"
              label="  "
            ></v-text-field>
          </v-col>
          <v-col cols="4">
            <span class="error-message" style="color: red" v-if="mapSheetNoValidationMessage"
              >map sheet no is required is Required</span
            >
          </v-col>
        </v-row>
        <v-row>
          <v-col md="2">
            <label>  <span>*</span></label>
          </v-col>
          <v-col md="4">
            <v-text-field
              v-model="newLandinfoarea.land_plot_no"
              :rules="[required('Land Plot No.')]"
              label="  "
            ></v-text-field>
          </v-col>
          <v-col cols="4">
            <span class="error-message" style="color: red" v-if="landPlotNoValidationMessage"
              >land plot no is required is Required</span
            >
          </v-col>
        </v-row>
        <v-row>
          <v-col md="12">
            
          </v-col>
        </v-row>
        <v-row>
          <v-col md="2">
            <v-text-field v-model="newLandinfoarea.area_ropani" label=""></v-text-field>
          </v-col>
          <v-col>
            
