          <v-col md="2">
            <v-text-field v-model="newLandinfoarea.area_daam" label=""></v-text-field>
          </v-col>
          <v-col>
            
          </v-col>
        </v-row>
        <v-row>
          <v-col md="2"></v-col>
          <v-col md="2">
            <v-text-field v-model="newLandinfoarea.area_bigha" label=""></v-text-field>
          </v-col>
          <v-col>
            
