          <v-col md="2">
            <v-text-field v-model="newLandinfoarea.area_dhur" label=""></v-text-field>
          </v-col>
          <v-col>
            
          </v-col>
        </v-row>
        <v-row>
          <v-col md="3"></v-col><v-col md="3"></v-col>
          <v-col md="2">
            <v-text-field v-model="newLandinfoarea.area_sqm" label="Area (sqm)"></v-text-field>
          </v-col>
          <v-col md="2">
            <v-text-field v-model="newLandinfoarea.area_sqft" label="Area (sqft)"></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="2">
            
          </v-col>
