            <v-text-field v-model="newLandinfoarea.remarks" label=""></v-text-field>
          </v-col>
          <v-col cols="2">
            Actual Setback (m)
          </v-col>
          <v-col cols="4">
            <v-text-field v-model="newLandinfoarea.actual_setback" label=""></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <v-btn
              primary
              v-if="isPostAuthorized"
              v-on:click="save"
              class="primary"
              :disabled="!valid"
              >Submit</v-btn
            >
          </v-col>
        </v-row>
        <br />
        <v-snackbar
          v-model="snackbar.show"
          :bottom="y === 'bottom'"
