<template>
  <div class="pa-10">
    <v-btn class="primary" :to="{ name: 'application_tab', params: { id: regId } }">Back</v-btn>
    <v-stepper v-model="el" class="vstepper">
      <v-layout justify-center>
        <v-flex xs3 class="mt-3">
          <v-stepper-header class="stepperHeader">
            <template v-for="n in steps" class="stepperHeaderTemplate">
              <v-card :key="n.sn" class="sober elevation-12 pt-1 pb-1">
                <v-stepper-step :key="n.sn" :step="n.sn" editable complete class="vstepperStep">
                  {{ n.text }}
                </v-stepper-step>
              </v-card>
            </template>
          </v-stepper-header>
        </v-flex>
        <v-flex xs10>
          <v-card>
            <v-stepper-items>
              <v-stepper-content v-for="n in steps" :key="`${n.sn}-content`" :step="n.sn" vertical>
                <component
                  :reg="application"
                  :isPostAuthorized="
                    userRoleId == 2 && reg.desk_user_role_id == userRoleId && reg.phase_id == 3
                  "
                  :is="getComponent(n.sn)"
                  v-bind:next-step="nextStep"
                  v-if="n.sn === el"
                  v-bind:registration-id="regId"
                  :id="id"
                />
              </v-stepper-content>
            </v-stepper-items>
          </v-card>
        </v-flex>
      </v-layout>
    </v-stepper>
  </div>
</template>
<script>
import PlinthInspectApplication from "./components/PlinthInspectApplication.vue";
import FirstPhaseReport from "./components/FirstPhaseReport.vue";
import SuperstructureApplication from "./components/SuperstructureNirmankoIjajatkolagiNibedan.vue";
import SuperstructureTipani from "./components/SuperstructureTipani.vue";
import PlinthInspectReport from "./components/PlinthInspectReport.vue";
import SuperstructurePermit from "./components/SuperstructurePermit.vue";

const COMPONENT_MAP = {
  1: "PlinthInspectApplication",
  2: "FirstPhaseReport",
  3: "SuperstructureApplication",
  4: "SuperstructureTipani",
  5: "PlinthInspectReport",
  6: "SuperstructurePermit"
};
export default {
  name: "PlinthDocumentUploadTab",
  props: {
    id: {
      type: [String, Number]
    },
    regId: {
      type: [String, Number]
    },
    sn: {
      type: [String, Number]
    }
  },
  data() {
    return {
      el: Number(this.sn),
      userRoleId: localStorage.getItem("auth_user_role_id"),
      // : this.regId,
      // id: this.id,
      reg: [],
      formData: new FormData(),
      steps: [
        { text: "     ", sn: 1 },
        { text: "   ..  ", sn: 2 },
        { text: "    ", sn: 3 },
        { text: "     ", sn: 4 },
        { text: "    ", sn: 5 },
        { text: "       ", sn: 6 }
      ]
      // { text: 'Ghardhanikophoto', sn: '11' }],
      // el: 1,
      // steps: 10,
    };
  },
  components: {
    PlinthInspectApplication,
    FirstPhaseReport,
    SuperstructureApplication,
    SuperstructureTipani,
    PlinthInspectReport,
    SuperstructurePermit
  },

  watch: {
    steps(val) {
      if (this.el > val) {
        this.el = val;
      }
    }
  },

  methods: {
    getComponent(n) {
      return COMPONENT_MAP[n];
    },
    nextStep(n) {
      if (n === this.steps.sn) {
        this.el = 1;
      } else {
        this.el = n + 1;
      }
    }
  },
  mounted() {
    const urlApp = `api/registration/${this.$route.params.regId}`;
    this.$axios.get(urlApp).then(response => {
      this.reg = response.data.data;
    });
  }
};
</script>

<style scoped>
.vstepperStep {
  width: 300px;
  padding: 15px;
}
