<template>
  <div>
    <v-card class="sober d-flex flex-column">
      <v-toolbar color="primary" dark flat>
        <v-toolbar-title>
          
        </v-toolbar-title>
      </v-toolbar>
      <v-form ref="form" class="mx-4" v-model="valid">
        <v-row>
          <v-col md="2">
            <label>Type<span>*</span></label>
          </v-col>
          <v-col md="4">
            <v-radio-group v-model="newsupervisor.is_consulting_firm" :mandatory="true">
              <v-radio label="Consulting Firm" v-bind:value="1"
                ><label><span>*</span></label></v-radio
              >
              <v-radio label="Freelancer" v-bind:value="0"></v-radio>
            </v-radio-group>
          </v-col>
          <v-col cols="4">
            <span class="error-message" style="color: red" v-if="isConsultingFirmValidationMessage"
              >Is consultingfirm is required is Required</span
            >
          </v-col>
        </v-row>
        <br />
        <br />
        <v-row>
          <v-col cols="4">
            Supervisor's Municipality Registration Number
          </v-col>
          <v-col cols="4">
            <v-text-field
              v-model="newsupervisor.registration_number"
              label="Supervisor's Registration Number"
              dense
            ></v-text-field>
          </v-col>
        </v-row>
        <br />
        <br />
        <v-row>
          <v-col cols="4">
             
          </v-col>
          <v-col cols="4">
            <!-- <v-select v-model="newsupervisor.affiliated_consultancy" :items="supervisor" item-value="id" item-text="affiliated_consultancy_name" :key="id"   ></v-select> -->
            <v-select
              v-model="newsupervisor.affiliated_consultancy_id"
              :items="consultancy"
              item-value="id"
              item-text="name"
              @change="consultancyDetail()"
              :key="id"
              label="Select  "
              dense
            ></v-select>
          </v-col>
        </v-row>
        <v-row hidden>
          <v-col cols="4">
             
          </v-col>
          <v-col cols="4">
            <v-text-field
              v-model="newsupervisor.affiliated_consultancy_name"
              label=" "
              dense
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="4">
            Supervisor's Council No
          </v-col>
          <v-col cols="4">
            <v-text-field
              v-model="newsupervisor.supervisor_council_number"
              label="Supervisor's Council No"
              dense
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col md="4">
            <label> Supervisor's Name<span>* </span></label>
          </v-col>
          <v-col md="4">
            <v-text-field
              v-model="newsupervisor.supervisor_name"
              :rules="[required('Supervisor Name')]"
              label="Supervisor's Name"
              dense
            ></v-text-field>
          </v-col>
          <v-col cols="4">
            <span class="error-message" style="color: red" v-if="supervisorNameValidationMessage"
              >supervisor name is Required</span
            >
          </v-col>
        </v-row>
        <v-snackbar
          v-model="snackbar.show"
          :bottom="y === 'bottom'"
          :color="color"
          :left="x === 'left'"
          :multi-line="mode === 'multi-line'"
          :right="x === 'right'"
          :timeout="3000"
          :top="y === 'top'"
          :vertical="mode === 'vertical'"
        >
          {{ successfulMessage }}
        </v-snackbar>
        <v-btn primary v-if="isPostAuthorized" v-on:click="save" class="primary" :disabled="!valid"
          >Submit</v-btn
        >
        <br />
      </v-form>
      <v-toolbar color="primary mt-2" dark flat>
        <v-toolbar-title>
          List
        </v-toolbar-title>
      </v-toolbar>
      <v-simple-table class="sober">
        <template v-slot:default>
          <thead>
            <tr class="registrationtable">
              <th class="text-left registrationtable">
                 
              </th>
              <th class="text-left registrationtable">
                Supervisor
              </th>
              <th class="text-left registrationtable">supervisor's Council No</th>
              <th class="text-left registrationtable">Action</th>
            </tr>
          </thead>
          <tbody>
            <tr
              class="registrationtable"
              v-for="supervisorList in supervisor"
              :key="supervisorList.id"
            >
              <td class="registrationtable">{{ supervisorList.affiliated_consultancy_name }}</td>
              <td class="registrationtable">
                {{ supervisorList.supervisor_name }}
              </td>
              <td>{{ supervisorList.supervisor_council_number }}</td>
              <td>
                <v-btn
                  small
                  color="primary"
                  v-if="isPostAuthorized"
                  v-on:click="edit(supervisorList.id)"
                  >Edit</v-btn
                >
                <v-btn
                  small
                  color="primary"
                  v-if="isPostAuthorized"
                  v-on:click="deleterecord(supervisorList.id)"
                  >Delete</v-btn
                >
                <v-btn small color="primary" v-else v-on:click="edit(supervisorList.id)"
                  >View</v-btn
                >
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-card>
  </div>
</template>
<script>
import { VueAxios } from "@/plugins/axios";

export default {
  name: "Supervisor",
  props: {
    nextStep: {
      type: Function
    },
    registrationId: {
      type: [String, Number]
    },
    id: {
      type: [String, Number]
    },
    application: {
      type: Object
    },
    isPostAuthorized: {
      type: Boolean
    }
  },
  data() {
    return {
      valid: false,
      required(propertyType) {
        return v => (v && v.length > 0) || `${propertyType} is required`;
      },
      requiredDropdown(propertyType) {
        return v => v || `${propertyType} is required`;
      },
      date: "",
      supervisor: [],
      consultancy: [],
      userRoleId: localStorage.getItem("auth_user_role_id"),
      newsupervisor: {
        id: 0,
        reg_id: this.registrationId,
        is_consulting_firm: "",
        registration_number: "",
        affiliated_consultancy_id: "",
        affiliated_consultancy_name: "",
        supervisor_name: "",
        supervisor_council_number: ""
      },
      isValid: true,
      isConsultingFirmValidationMessage: false,
      supervisorNameValidationMessage: false,
      successfulMessage: "",
      snackbar: {
        show: false,
        message: this.sucessfulMessage,
        color: "success"
      }
    };
  },
  created() {},
  mounted() {
    const url = `api/registration/${this.registrationId}/supervisor`;
    VueAxios.get(url)
      .then(response => {
        // debugger;
        this.supervisor = response.data.data;
        // this.newsupervisor.clear();
      })
      .catch(error => {
        console.log(`api error:${error}`);
      });
    const consultancyUrl = "api/consultancy";
    VueAxios.get(consultancyUrl)
      .then(response => {
        // debugger;
        this.consultancy = response.data.data;
        console.log(this.consultancy);
      })
      .catch(error => {
        console.log(`api error:${error}`);
      });
  },
  methods: {
    save() {
      //   debugger;
      if (
        this.newsupervisor.id === "null" ||
        this.newsupervisor.id === 0 ||
        this.newsupervisor.id === undefined
      ) {
        const saveUrl = `/api/registration/${this.registrationId}/supervisor`;
        VueAxios.post(saveUrl, this.newsupervisor)
          .then(response => {
            // debugger;
            if (response.status === 200 || response.status === 201) {
              this.supervisor.push(response.data.data);
              this.snackbar.show = true;
              this.successfulMessage = "Record added successfuly.";
              this.$refs.form.reset();
            }
          })
          .catch(error => {
            // alert(2);
            console.log(`api error:${error}`);
          });
      } else {
        const eurl = `/api/registration/${this.$route.params.regId}/supervisor/${this.newsupervisor.id}`;
        VueAxios.put(
          eurl,
          this.newsupervisor,
          {
            headers: {
              "X-Requested-With": "XMLHttpRequest",
              Authorization: `Bearer ${window.localStorage.getItem("token")}`
            },
            _method: "patch"
          },
          {}
        )
          .then(response => {
            // debugger;
            if (response.status === 200 || response.status === 201) {
              const i = this.supervisor.map(item => item.id).indexOf(this.newsupervisor.id); // find index of your object
              this.supervisor[
                i
              ].affiliated_consultancy_name = this.newsupervisor.affiliated_consultancy_name;
              this.supervisor[i].registration_number = this.newsupervisor.registration_number;
              this.supervisor[
                i
              ].supervisor_council_number = this.newsupervisor.supervisor_council_number;
              this.supervisor[i].supervisor_name = this.newsupervisor.supervisor_name;
              this.supervisor[i].is_consulting_firm = this.newsupervisor.is_consulting_firm;
              this.supervisor[
                i
              ].affiliated_consultancy_id = this.newsupervisor.affiliated_consultancy_id;
              this.snackbar.show = true;
              this.successfulMessage = "Data updated successfully";
              this.$refs.form.reset();
            }
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    consultancyDetail() {
      const url = `api/consultancy/${this.newsupervisor.affiliated_consultancy_id}`;
      VueAxios.get(url).then(response => {
        this.newsupervisor.affiliated_consultancy_name = response.data.data.name;
        this.newsupervisor.registration_number = response.data.data.registration_number;
        console.log(response.data.data);
      });
    },
    edit(id) {
      //   debugger;
      //    const url = `api/registration/${this.$route.params.regId}/supervisor/${this.$route.params.id}`;
      const url = `api/registration/${this.$route.params.regId}/supervisor/${id}`;
      VueAxios.get(url).then(response => {
        // debugger;
        this.newsupervisor = response.data.data;
        console.log(response.data.data);
      });
    },
    deleterecord(id) {
      const deleteurl = `api/registration/${this.$route.params.regId}/supervisor/${id}`;
      VueAxios.delete(deleteurl)
        .then(response => {
          // debugger;
          if (response.status === 200 || response.status === 201) {
            this.snackbar.show = true;
            this.successfulMessage = "Data deleted successfully";
            const i = this.supervisor.map(item => item.id).indexOf(id); // find index of your object
            this.supervisor.splice(i, 1);
          }
          console.log(response);
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>
