<template>
  <div>
    <v-card class="sober d-flex flex-column">
      <v-toolbar color="primary" dark flat>
        <v-toolbar-title>
          
        </v-toolbar-title>
      </v-toolbar>
      <v-form ref="form" class="mx-4" v-model="valid">
        <v-row>
          <v-col md="4">
            <label>Type<span>*</span></label>
          </v-col>
          <v-col md="4">
            <v-radio-group dense v-model="newDesigner.is_consulting_firm" :mandatory="true">
              <v-radio label="Consulting Firm" v-bind:value="1"></v-radio>
              <v-radio label="Freelancer" v-bind:value="0"></v-radio>
            </v-radio-group>
          </v-col>
          <v-col cols="4">
            <span class="error-message" style="color: red" v-if="isConsultingFirmValidationMessage"
              >Is consultingfirm is required is Required</span
            >
          </v-col>
        </v-row>
        <br />
        <br />
        <v-row>
          <v-col cols="4">
            <label> Designer's Municipality Registration No</label>
          </v-col>
          <v-col cols="4">
            <v-text-field
              v-model="newDesigner.registration_number"
              label="Designer's Registration No"
              dense
            ></v-text-field>
          </v-col>
          <br />
          <v-col cols="4">
            <span
              class="error-message"
              style="color: red"
              v-if="registrationNumberValidationMessage"
              >Registration No is Required</span
            >
          </v-col>
        </v-row>
        <br />
        <v-row>
          <v-col cols="4">
             
          </v-col>
          <v-col cols="4">
            <v-select
              v-model="newDesigner.affiliated_consultancy"
              label="Select  "
              :items="consultancy"
              item-value="id"
              item-text="name"
              @change="consultancyDetail()"
              :key="id"
              dense
            ></v-select>
          </v-col>
        </v-row>
        <v-row hidden>
          <v-col cols="4">
             
          </v-col>
          <v-col cols="4">
            <v-text-field
              v-model="newDesigner.affiliated_consultancy_name"
              label=" "
              dense
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="4">
            Designer's Council No
          </v-col>
          <v-col cols="4">
            <v-text-field
              v-model="newDesigner.designer_council_number"
              label="Designer's Council No"
              dense
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col md="4">
            <label> Designer's Name <span>*</span></label>
          </v-col>
          <v-col md="4">
            <v-text-field
              v-model="newDesigner.designer_name"
              :rules="[required('Designer Name')]"
              label="Designer's Name"
              dense
            ></v-text-field>
          </v-col>
          <v-col cols="4">
            <span class="error-message" style="color: red" v-if="designerNameValidationMessage"
              >designer name is Required</span
            >
          </v-col>
        </v-row>
        <v-snackbar
          v-model="snackbar.show"
          :bottom="y === 'bottom'"
          :color="color"
          :left="x === 'left'"
          :multi-line="mode === 'multi-line'"
          :right="x === 'right'"
          :timeout="3000"
          :top="y === 'top'"
          :vertical="mode === 'vertical'"
        >
          {{ successfulMessage }}
        </v-snackbar>
        <!-- <v-btn primary v-on:click="save" style="width:100px;height:45px;" class="primary">Submit</v-btn> -->
        <v-btn primary v-if="isPostAuthorized" v-on:click="save" class="primary" :disabled="!valid"
          >Submit</v-btn
        ><br />
      </v-form>
      <v-toolbar color="primary mt-2" dark flat>
        <v-toolbar-title>
          List
        </v-toolbar-title>
      </v-toolbar>
      <v-simple-table class="sober">
        <template v-slot:default>
          <thead>
            <tr class="registrationtable">
              <th class="text-left registrationtable">
                 
              </th>
              <th class="text-left registrationtable">
                Designer's Municipality Registration Number
              </th>
              <th class="text-left registrationtable">Designer's Council No</th>
              <th class="text-left registrationtable">Designer's Name</th>
              <th class="text-left registrationtable">Action</th>
            </tr>
          </thead>
          <tbody>
            <tr class="registrationtable" v-for="designerList in designer" :key="designerList.id">
              <td class="registrationtable">{{ designerList.affiliated_consultancy_name }}</td>
              <td class="registrationtable">
                {{ designerList.registration_number }}
              </td>
              <td class="registrationtable">{{ designerList.designer_council_number }}</td>
              <td class="registrationtable">{{ designerList.designer_name }}</td>
              <td>
                <v-btn
                  small
                  class="ma-1"
                  color="primary"
                  v-if="isPostAuthorized"
                  v-on:click="edit(designerList.id)"
                  >Edit</v-btn
                >
                <v-btn
                  small
                  color="primary"
                  v-if="isPostAuthorized"
                  v-on:click="deleterecord(designerList.id)"
                  >Delete</v-btn
                >
                <v-btn small color="primary" v-else v-on:click="edit(designerList.id)">View</v-btn>
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-card>
  </div>
</template>
<script>
import { VueAxios } from "@/plugins/axios";

export default {
  name: "Designer",
  props: {
    nextStep: {
      type: Function
    },
    registrationId: {
      type: [String, Number]
    },
    id: {
      type: [String, Number]
    },
    application: {
      type: Object
    },
    isPostAuthorized: {
      type: Boolean
    }
  },

  data() {
    return {
      valid: false,
      required(propertyType) {
        return v => (v && v.length > 0) || `${propertyType} is required`;
      },
      requiredDropdown(propertyType) {
        return v => v || `${propertyType} is required`;
      },
      date: "",
      designer: [],
      consultancy: [],
      results: [],
      userRoleId: localStorage.getItem("auth_user_role_id"),
      newDesigner: {
        id: 0,
        reg_id: this.registrationId,
        is_consulting_firm: "",
        registration_number: "",
        affiliated_consultancy: "",
        affiliated_consultancy_name: "",
        designer_name: "",
        designer_council_number: ""
      },
      isValid: true,
      isConsultingFirmValidationMessage: false,
      registrationNumberValidationMessage: false,
      designerNameValidationMessage: false
    };
  },
  mounted() {
    const url = `api/registration/${this.registrationId}/designer`;
    VueAxios.get(url)
      .then(response => {
        // debugger;
        this.designer = response.data.data;
      })
      .catch(error => {
        console.log(`api error:${error}`);
      });
    const consultancyUrl = "api/consultancy";
    VueAxios.get(consultancyUrl)
      .then(response => {
        // debugger;
        this.consultancy = response.data.data;
        // this.newDesigner.clear();
      })
      .catch(error => {
        console.log(`api error:${error}`);
      });
  },
  methods: {
    save() {
      //   debugger;
      if (
        this.newDesigner.id === "null" ||
        this.newDesigner.id === 0 ||
        this.newDesigner.id === undefined
      ) {
        const saveUrl = `/api/registration/${this.registrationId}/designer`;
        VueAxios.post(saveUrl, this.newDesigner)
          .then(response => {
            // debugger;
            if (response.status === 200 || response.status === 201) {
              this.designer.push(response.data.data);
              this.snackbar.show = true;
              this.successfulMessage = "Successful.";
              this.$refs.form.reset();
            }
          })
          .catch(error => {
            // alert(2);
            console.log(`api error:${error}`);
          });
      } else {
        const eurl = `/api/registration/${this.$route.params.regId}/designer/${this.newDesigner.id}`;
        VueAxios.put(
          eurl,
          this.newDesigner,
          {
            headers: {
              "X-Requested-With": "XMLHttpRequest",
              Authorization: `Bearer ${window.localStorage.getItem("token")}`
            },
            _method: "patch"
          },
          {}
        )
          .then(response => {
            // debugger;
            if (response.status === 200 || response.status === 201) {
              // const currentIndex = this.designer.indexOf(this);
              // const tempDesigner = this.designer.find(item => item.id === id);
              const i = this.designer.map(item => item.id).indexOf(this.newDesigner.id); // find index of your object
              this.designer[
                i
              ].affiliated_consultancy_name = this.newDesigner.affiliated_consultancy_name;
              this.designer[i].registration_number = this.newDesigner.registration_number;
              this.designer[i].designer_council_number = this.newDesigner.designer_council_number;
              this.designer[i].designer_name = this.newDesigner.designer_name;
              // this.designer.$set(i, this.newDesigner);
              // this.designer.splice(i, 1, this.newDesigner);
              this.snackbar.show = true;
              this.successfulMessage = "Data updated successfully";
              this.$refs.form.reset();
            }
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    consultancyDetail() {
      const url = `api/consultancy/${this.newDesigner.affiliated_consultancy}`;
      VueAxios.get(url).then(response => {
        // debugger;
        this.newDesigner.affiliated_consultancy_name = response.data.data.name;
        this.newDesigner.registration_number = response.data.data.registration_number;
        console.log(response.data.data);
      });
    },
    edit(id) {
      //   debugger;
      //    const url = `api/registration/${this.$route.params.regId}/designer/${this.$route.params.id}`;
      const url = `api/registration/${this.$route.params.regId}/designer/${id}`;
      VueAxios.get(url).then(response => {
        // debugger;
        this.newDesigner = response.data.data;
        console.log(response.data.data);
      });
    },
    deleterecord(id) {
      const deleteurl = `api/registration/${this.$route.params.regId}/designer/${id}`;
      VueAxios.delete(deleteurl)
        .then(response => {
          // debugger;
          if (response.status === 200 || response.status === 201) {
            this.snackbar.show = true;
            this.successfulMessage = "Data deleted successfully";
            const i = this.designer.map(item => item.id).indexOf(id); // find index of your object
            this.designer.splice(i, 1);
          }
          console.log(response);
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>

