<template>
  <div>
    <v-card class="mb-12" color="grey lighten-1" height="auto">
      <v-app id="inspire">
        <v-form>
          <v-container>
            <v-card class="sober d-flex flex-column">
              <v-toolbar color="primary" dark flat>
                <v-toolbar-title>
                             
                  
                </v-toolbar-title>
              </v-toolbar>
              <v-form ref="form" class="mx-4">
                <v-row>
                  <v-col md="2">
                    <label> Document<span>*</span></label>
                  </v-col>
                  <v-col cols="6">
                    <input type="file" id="file-upload" @change="onFileChange" />
                  </v-col>
                  <v-col cols="4">
                    <img :src="uploadedDocument" width="100px" />
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="2">
                    <label> Remarks<span>*</span></label>
                  </v-col>

                  <v-col cols="6">
                    <v-textarea v-model="document.remarks" label="" height="10" solo></v-textarea>
                  </v-col>
                </v-row>
                <v-btn primary v-on:click="save" v-if="isPostAuthorized" class="primary"
                  >Submit</v-btn
                >
                <v-snackbar
                  v-model="snackbar.show"
                  :bottom="y === 'bottom'"
                  :color="color"
                  :left="x === 'left'"
                  :multi-line="mode === 'multi-line'"
                  :right="x === 'right'"
                  :timeout="3000"
                  :top="y === 'top'"
                  :vertical="mode === 'vertical'"
                >
                  {{ successfulMessage }}
                </v-snackbar>
              </v-form>
              <v-toolbar color="primary mt-2" dark flat dense>
                <v-toolbar-title>
                  List
                </v-toolbar-title>
              </v-toolbar>
              <v-simple-table class="sober">
                <template v-slot:default>
                  <thead>
                    <tr>
                      <th class="text-left">
                        Document
                      </th>
                      <th class="text-left">
                        Remarks
                      </th>
                      <th class="text-left">Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr
                      class="registrationtable"
                      v-for="manjurinama in manjurinamaList"
                      :key="manjurinama.id"
                    >
                      <td class="registrationtable">
                        <a :href="basePath + manjurinama.document_data" target="_blank">{{
                          manjurinama.document_data
                        }}</a>
                      </td>
                      <td class="registrationtable">
                        {{ manjurinama.remarks }}
                      </td>
                      <td>
                        <v-btn
                          small
                          class="ma-1"
                          color="primary"
                          v-if="isPostAuthorized"
                          v-on:click="edit(manjurinama.id)"
                          >Edit</v-btn
                        >
                        <v-btn
                          small
                          color="primary"
                          v-if="isPostAuthorized"
                          v-on:click="deleterecord(manjurinama.id)"
                          >Delete</v-btn
                        >
                        <br />
                        <br />
                      </td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </v-card>
          </v-container>
        </v-form>
        <br />
        <br />
      </v-app>
    </v-card>
    <v-card hidden>
      <v-btn color="primary" @click="nextStep(1)">
        Continue
      </v-btn>

      <v-btn text>Cancel</v-btn>
    </v-card>
  </div>
</template>
<script>
import { VueAxios } from "@/plugins/axios";

export default {
  name: "Jaggadhani",
  //     components: {
  //   Jaggadhani,
  // },
  props: {
    registrationId: {
      type: [String, Number]
    },
    id: {
      type: [String, Number]
    },
    reg: {
      type: Object
    },
    isPostAuthorized: {
      type: Boolean
    }
  },
  data: () => ({
    userRoleId: localStorage.getItem("auth_user_role_id"),
    successfulMessage: "",
    snackbar: {
      show: false,
      message: this.sucessfulMessage,
      color: "success"
    },
    document: {
      created_by_user_id: 0,
      created_date: null,
      document_master_id: 0,
      edited_by_user_id: null,
      edited_date: null,
      id: 0,
      reg_id: 0,
      remarks: "           "
    },
    basePath: process.env.VUE_APP_BASE_URL,
    uploadedDocument: "",
    formData: new FormData(),
    document_data: "",
    manjurinamaList: [
      {
        created_by_user_id: 0,
        created_date: null,
        document_master_id: 0,
        edited_by_user_id: null,
        edited_date: null,
        id: 0,
        reg_id: 0,
        remarks: ""
      }
    ]
  }),
  mounted() {
    if (!this.isPostAuthorized) {
      this.document.remarks = ""; //empty the remarks if the user is only allowed to view
    }
    const url = `api/registration/${this.$route.params.regId}/documents/manjurinama`;
    VueAxios.get(url, {
      headers: {
        "X-Requested-With": "XMLHttpRequest",
        Authorization: `Bearer ${localStorage.getItem("token")}`
      }
    })
      .then(response => {
        // this.uploadedDocument = this.basePath + response.data.data[0].document_data;
        this.manjurinamaList = response.data.data;
        console.log(response.data);
      })
      .catch(() => {
        // this.uploadedDocument = '';
      });
  },
  methods: {
    onFileChange(e) {
      const self = this;
      self.formData.append("remarks", this.document.remarks);
      const files = e.target.files || e.dataTransfer.files;
      console.log(files[0].size);
      if (files.length > 0 && files[0].size < 1024 * 1024 * 5) {
        for (let i = 0; i < files.length; i += 1) {
          self.formData.append("document_data", files[i], files[i].name);
        }
      } else {
        document.getElementById("file-upload").value = "";
        // alert('file greater than 5mb');
      }
    },
    clearSelected() {
      this.document.created_by_user_id = 0;
      this.document.created_date = null;
      this.document.document_master_id = 0;
      this.document.edited_by_user_id = null;
      this.document.edited_date = null;
      this.document.id = 0;
      this.document.reg_id = 0;
      this.document.remarks = "";
    },
    save() {
      // debugger;
      const self = this;
      self.formData.append("remarks", this.document.remarks);
      if (this.document.id === 0) {
        const urlSave = `api/registration/${this.$route.params.regId}/documents/manjurinama`;
        VueAxios.post(urlSave, self.formData, {
          headers: {
            "X-Requested-With": "XMLHttpRequest",
            Authorization: `Bearer ${localStorage.getItem("token")}`
          }
        })
          .then(response => {
            this.snackbar.show = true;
            this.successfulMessage = "Successfully Updated.";
            this.uploadedDocument = this.basePath + response.data.data.document_data;
            this.manjurinamaList.push({
              document_data: response.data.data.document_data,
              remarks: response.data.data.remarks,
              id: response.data.data.id
            });
            this.clearSelected();
          })
          .catch(() => {
            // console.log(error);
          });
      } else {
        self.formData.append("_method", "PUT");
        const urlEdit = `api/registration/${this.$route.params.regId}/documents/manjurinama/${this.document.id}`;
        VueAxios.post(urlEdit, self.formData, {
          headers: {
            "X-Requested-With": "XMLHttpRequest",
            Authorization: `Bearer ${localStorage.getItem("token")}`
          }
        })
          .then(response => {
            this.snackbar.show = true;
            this.successfulMessage = "Successfuly Updated.";
            this.uploadedDocument = this.basePath + response.data.data.document_data;
            this.clearSelected();
          })
          .catch(() => {
            // console.log(error);
          });
      }
    },
    edit(id) {
      this.document = this.manjurinamaList.filter(c => c.id === id).pop();
      this.uploadedDocument = this.basePath + this.document.document_data;
      // console.log(this.document);
    },
    deleterecord(id) {
      const deleteurl = `api/registration/${this.$route.params.regId}/documents/manjurinama/${id}`;
      VueAxios.delete(deleteurl)
        .then(response => {
          if (response.status === 200 || response.status === 201) {
            this.snackbar.show = true;
            this.successfulMessage = "Data deleted successfully";
            const i = this.manjurinamaList.map(item => item.id).indexOf(id); // find index of your object
            this.manjurinamaList.splice(i, 1);
          }
          // console.log(response);
        })
        .catch(() => {
          // console.log(error);
        });
    }
  }
};
</script>

