        </v-row>
        <v-row>
          <v-col md="4">
            <label> Father's/Husband's Name <span>*</span></label>
          </v-col>
          <v-col md="4">
            <v-text-field
              v-model="newhouseownerinfo.father_or_husband_name"
              :rules="[required('Father/Husband Name')]"
