              dense
            ></v-text-field>
          </v-col>
        </v-row>
        <br />
        <v-row>
          <v-col md="4">
             
          </v-col>
          <v-col md="4">
            <v-text-field
              v-model="newhouseownerinfo.grandfather_name_np"
              label=" "
              dense
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col md="4">
            Grandfather's Name
          </v-col>
          <v-col md="4">
            <v-text-field
              v-model="newhouseownerinfo.grandfather_name"
              label="Grandfather's Name"
              dense
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col md="4">
            <label>    <span>*</span></label>
          </v-col>
          <v-col md="4">
            <!-- <v-text-field v-model="newhouseownerinfo.citizenship_district_id" label=""     ></v-text-field>
                                 -->
            <v-select
              v-model="newhouseownerinfo.citizenship_district_id"
              label="  "
              :rules="[requiredDropdown('Citizenship District')]"
              :items="districts"
              item-value="id"
              item-text="district_name"
              :key="id"
              dense
            ></v-select>
          </v-col>
          <v-col cols="4">
            <span
              class="error-message"
              style="color: red"
              v-if="citizenshipDistrictIdValidationMessage"
              >District is Required</span
            >
          </v-col>
        </v-row>
        <v-row>
          <v-col md="4">
            <label> Phone <span>*</span></label>
          </v-col>
          <v-col md="4">
            <v-text-field
              v-model="newhouseownerinfo.phone_no"
              :rules="[required('Phone No.')]"
              type="text"
              label="Phone"
              dense
            ></v-text-field>
          </v-col>
          <v-col cols="4">
            <span class="error-message" style="color: red" v-if="phoneValidationMessage"
              >Phone is Required</span
            >
          </v-col>
        </v-row>
        <v-row>
          <v-col md="4">
            <label>  . <span>*</span></label>
          </v-col>
          <v-col md="4">
            <v-text-field
              v-model="newhouseownerinfo.citizenship_no"
              :rules="[required('Citizenship No.')]"
              label=" ."
              dense
            ></v-text-field>
          </v-col>
          <v-col cols="4">
            <span class="error-message" style="color: red" v-if="citizenshipNoValidationMessage"
              >Citizenship number is Required</span
            >
          </v-col>
        </v-row>
        <v-row>
          <v-col md="4">
            <label> Ward Number <span>*</span></label>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field
              v-model="newhouseownerinfo.ward_no"
              :rules="[requiredNumber('Ward No. ')]"
              type="number"
              label="Ward Number"
              dense
              required
            ></v-text-field>
          </v-col>
          <v-col cols="4">
            <span class="error-message" style="color: red" v-if="wardNoValidationMessage"
              >Ward no is Required</span
            >
          </v-col>
        </v-row>
        <v-row>
          <v-col md="4">
            <label>    <span>*</span></label>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field
              v-model="newhouseownerinfo.citizenship_issue_date_np"
              :rules="[required('Citizenship Date')]"
              label="  "
              dense
              class="custom-text-field"
              ref="dateField"
              required
            ></v-text-field>
          </v-col>
          <v-col cols="4">
            <span
              class="error-message"
              style="color: red"
              v-if="citizenshipIssueDateNpValidationMessage"
              >citizenship issue date is Required</span
            >
          </v-col>
        </v-row>
        <v-row>
          <v-col md="4">
            <label> Address <span>*</span></label>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field
              v-model="newhouseownerinfo.address"
              :rules="[required('Address')]"
              label="Address"
              dense
              required
            ></v-text-field>
          </v-col>
          <v-col cols="4">
            <span class="error-message" style="color: red" v-if="addressValidationMessage"
              ><Address></Address> is Required</span
            >
          </v-col>
        </v-row>
        <v-row>
          <v-col md="4">
            <label>  <span>*</span></label>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field
              v-model="newhouseownerinfo.tole"
              :rules="[required('Tole')]"
              label=""
              dense
              tole
              required
            ></v-text-field>
          </v-col>
          <v-col cols="4">
            <span class="error-message" style="color: red" v-if="toleValidationMessage"
              >Tole is Required</span
            >
          </v-col>
        </v-row>
        <v-row>
          <v-col md="4">
            <label>   <span>*</span></label>
          </v-col>
          <v-col cols="4">
            <v-text-field
              v-model="newhouseownerinfo.house_no"
              label=" "
              :rules="[required('House No.')]"
              dense
              class="custom-text-field date-picker"
              required
            ></v-text-field>
          </v-col>
          <v-col cols="4">
            <span class="error-message" style="color: red" v-if="houseNoValidationMessage"
              >House No is Required</span
            >
          </v-col>
        </v-row>
        <v-row>
          <v-col md="4">
            <label>  </label>
          </v-col>
          <v-col cols="4">
            <input type="file" id="file-upload" @change="onFileChange" />
          </v-col>
        </v-row>
        <v-snackbar
          v-model="snackbar.show"
          :bottom="y === 'bottom'"
          :color="color"
          :left="x === 'left'"
          :multi-line="mode === 'multi-line'"
          :right="x === 'right'"
          :timeout="3000"
          :top="y === 'top'"
          :vertical="mode === 'vertical'"
        >
          {{ successfulMessage }}
        </v-snackbar>
        <v-btn primary v-if="isPostAuthorized" v-on:click="save" class="primary" :disabled="!valid"
          >Submit</v-btn
        >
        <br />
      </v-form>
      <v-toolbar color="primary mt-2" dark flat>
        <v-toolbar-title>
          List
        </v-toolbar-title>
      </v-toolbar>
      <v-simple-table class="sober">
        <template v-slot:default>
          <thead>
            <tr class="registrationtable">
              <th class="text-left registrationtable">
                 
              </th>
              <th class="text-left registrationtable">
                
              </th>
              <th class="text-left registrationtable"> </th>
              <th class="text-left registrationtable">  </th>
              <th class="text-left registrationtable">Action</th>
            </tr>
          </thead>
          <tbody>
            <tr
              class="registrationtable"
              v-for="houseownerinfoList in houseownerinfo"
              :key="houseownerinfoList.id"
            >
              <td class="registrationtable">{{ houseownerinfoList.name }}</td>
              <td>{{ houseownerinfoList.phone_no }}</td>
              <td>{{ houseownerinfoList.citizenship_no }}</td>
              <td class="registrationtable">
                {{ houseownerinfoList.citizenship_issue_date_np }}
              </td>
              <td>
                <v-btn
                  small
                  color="primary"
                  v-if="isPostAuthorized"
                  v-on:click="edit(houseownerinfoList.id)"
                  >Edit</v-btn
                >
                <v-btn
                  small
                  color="primary"
                  v-if="isPostAuthorized"
                  v-on:click="deleterecord(houseownerinfoList.id)"
                  >Delete</v-btn
                >
                <v-btn small color="primary" v-else v-on:click="edit(houseownerinfoList.id)"
                  >View</v-btn
                >
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-card>
  </div>
</template>
<script>
import { VueAxios } from "@/plugins/axios";
//   import * as nepaliDatePicker from '../../../../node_modules/nepali-date-picker/dist/jquery.nepaliDatePicker.min'; /*eslint-disable-line*/
//  import '../../../../node_modules/nepali-date-picker/dist/nepaliDatePicker.min.css';

export default {
  name: "Houseownerinfo",
  props: {
    nextStep: {
      type: Function
    },
    registrationId: {
      type: [String, Number]
    },
    id: {
      type: [String, Number]
    },
    application: {
      type: Object
    },
    isPostAuthorized: {
      type: Boolean
    }
  },

  data() {
    return {
      valid: false,
      required(propertyType) {
        return v => (v && v.length > 0) || `${propertyType} is required`;
      },
      requiredDropdown(propertyType) {
        return v => v || `${propertyType} is required`;
      },
      requiredNumber(propertyType) {
        return v => (v && v > 0) || `${propertyType} is required`;
      },
      date: "",
      options: [
        { id: 1, value: "Mr" },
        { id: 2, value: "Mrs" },
        { id: 3, value: "Ms" }
      ],
      error: "",
      isValid: true,
      districts: [],
      photo_name: "",
      formData: new FormData(),
      userRoleId: localStorage.getItem("auth_user_role_id"),
      houseownerinfo: [],
      salutationValidationMessage: false,
      name: false,
      nameNp: false,
      fatherNameNpValidationMessage: false,
      fatherNameValidationMessage: false,
      citizenshipNoValidationMessage: false,
      citizenshipDistrictIdValidationMessage: false,
      phoneValidationMessage: false,
      wardNoValidationMessage: false,
      citizenshipIssueDateNpValidationMessage: false,
      addressValidationMessage: false,
      toleValidationMessage: false,
      houseNoValidationMessage: false,
      newhouseownerinfo: {
        id: 0,
        reg_id: this.registrationId,
        salutation: "",
        citizenship_no: "",
        name: "",
        name_np: "",
        father_or_husband_name: "",
        father_or_husband_name_np: "",
        grandfather_name: "",
        grandfather_name_np: "",
        citizenship_district_id: "",
        phone_no: "",
        citizenship_issue_date: "2016-8-8",
        citizenship_issue_date_np: "",
        address: "",
        tole: "",
        ward_no: "",
        house_no: "",
        photo: ""
      },
      successfulMessage: "",
      snackbar: {
        show: false,
        message: this.sucessfulMessage,
        color: "success"
      }
    };
  },
  created() {
    const url = `api/registration/${this.registrationId}/houseowner-detail`;
    VueAxios.get(url)
      .then(response => {
        // debugger;
        this.houseownerinfo = response.data.data;
        //   this.newhouseownerinfo.clear();
      })
      .catch(error => {
        console.log(`api error:${error}`);
      });
    const districtUrl = "api/districts";
    VueAxios.get(districtUrl)
      .then(response => {
        // debugger;
        this.districts = response.data.data;
      })
      .catch(error => {
        console.log(`api error:${error}`);
      });
  },
  mounted() {
    const { dateField } = this.$refs;
    if (dateField) {
      $(dateField.$el).nepaliDatePicker({
        dateFormat: "%y-%m-%d",
        closeOnDateSelect: true
      });
      $(dateField.$el).on("dateSelect", event => {
        this.newhouseownerinfo.citizenship_issue_date_np = event.datePickerData.formattedDate;
      });
    }
  },
  methods: {
    onFileChange(e) {
      const self = this;
      //   self.formData.append('photo', this.newhouseownerinfo.photo);
      const files = e.target.files || e.dataTransfer.files;
      console.log(files[0].size);
      if (files.length > 0 && files[0].size < 1024 * 1024 * 5) {
        for (let i = 0; i < files.length; i += 1) {
          self.formData.append("photo", files[i], files[i].name);
        }
      } else {
        document.getElementById("file-upload").value = "";
        // alert('file greater than 5mb');
      }
    },
    save() {
      // debugger;
      // console.log(this.formData);
      const self = this;
      self.formData.append("salutation", this.newhouseownerinfo.salutation);
      self.formData.append("citizenship_no", this.newhouseownerinfo.citizenship_no);
      self.formData.append("name", this.newhouseownerinfo.name);
      self.formData.append("name_np", this.newhouseownerinfo.name_np);
      self.formData.append("father_or_husband_name", this.newhouseownerinfo.father_or_husband_name);
      self.formData.append(
        "father_or_husband_name_np",
        this.newhouseownerinfo.father_or_husband_name_np
      );
      self.formData.append("grandfather_name", this.newhouseownerinfo.grandfather_name);
      self.formData.append("grandfather_name_np", this.newhouseownerinfo.grandfather_name_np);
      self.formData.append(
        "citizenship_district_id",
        this.newhouseownerinfo.citizenship_district_id
      );
      self.formData.append("phone_no", this.newhouseownerinfo.phone_no);
      self.formData.append("citizenship_issue_date", this.newhouseownerinfo.citizenship_issue_date);
      self.formData.append(
        "citizenship_issue_date_np",
        this.newhouseownerinfo.citizenship_issue_date_np
      );
      self.formData.append("address", this.newhouseownerinfo.address);
      self.formData.append("tole", this.newhouseownerinfo.tole);
      self.formData.append("ward_no", this.newhouseownerinfo.ward_no);
      self.formData.append("house_no", this.newhouseownerinfo.house_no);

      if (
        this.newhouseownerinfo.id === "null" ||
        this.newhouseownerinfo.id === 0 ||
        this.newhouseownerinfo.id === undefined
      ) {
        const saveUrl = `/api/registration/${this.$route.params.regId}/houseowner-detail`;
        VueAxios.post(
          saveUrl,
          self.formData,
          {
            headers: {
              "X-Requested-With": "XMLHttpRequest",
              Authorization: `Bearer ${window.localStorage.getItem("token")}`
            }
          },
          {
            timeout: 10000
          }
        )
          .then(response => {
            //  debugger;
            if (response.status === 200 || response.status === 201) {
              this.houseownerinfo.push(response.data.data);
              this.snackbar.show = true;
              this.successfulMessage = "Record added successfully.";
              this.$refs.form.reset();
            }
          })
          .catch(error => {
            console.log(`api error:${error}`);
          });
      } else {
        self.formData.append("_method", "PUT"); //Method spoofing to insert files via post for edit
        const eurl = `/api/registration/${this.$route.params.regId}/houseowner-detail/${this.newhouseownerinfo.id}`;
        VueAxios.post(eurl, this.newhouseownerinfo)
          .then(response => {
            // debugger;
            if (response.status === 200 || response.status === 201) {
              const i = this.houseownerinfo.map(item => item.id).indexOf(this.newhouseownerinfo.id); // find index of your object
              this.houseownerinfo[i].citizenship_no = this.newhouseownerinfo.citizenship_no;
              this.houseownerinfo[i].name = this.newhouseownerinfo.name;
              this.houseownerinfo[i].name_np = this.newhouseownerinfo.name_np;
              this.houseownerinfo[
                i
              ].father_or_husband_name = this.newhouseownerinfo.father_or_husband_name;
              this.houseownerinfo[
                i
              ].father_or_husband_name_np = this.newhouseownerinfo.father_or_husband_name_np;
              this.houseownerinfo[i].grandfather_name = this.newhouseownerinfo.grandfather_name;
              this.houseownerinfo[
                i
              ].grandfather_name_np = this.newhouseownerinfo.grandfather_name_np;
              this.houseownerinfo[
                i
              ].citizenship_district_id = this.newhouseownerinfo.citizenship_district_id;
              this.houseownerinfo[i].phone_no = this.newhouseownerinfo.phone_no;
              this.houseownerinfo[
                i
              ].citizenship_issue_date_np = this.newhouseownerinfo.citizenship_issue_date_np;
              this.houseownerinfo[i].address = this.newhouseownerinfo.address;
              this.houseownerinfo[i].tole = this.newhouseownerinfo.tole;
              this.houseownerinfo[i].ward_no = this.newhouseownerinfo.ward_no;
              this.houseownerinfo[i].house_no = this.newhouseownerinfo.house_no;
              this.snackbar.show = true;
              this.successfulMessage = "Data updated successfully";
              this.$refs.form.reset();
            }
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    edit(id) {
      //    const url = `api/registration/${this.$route.params.regId}/houseownerinfo/${this.$route.params.id}`;
      const url = `api/registration/${this.$route.params.regId}/houseowner-detail/${id}`;
      VueAxios.get(url).then(response => {
        this.newhouseownerinfo = response.data.data;
        console.log(response.data.data.photo);
      });
    },
    deleterecord(id) {
      const deleteurl = `api/registration/${this.$route.params.regId}/houseowner-detail/${id}`;
      VueAxios.delete(deleteurl)
        .then(response => {
          // debugger;
          if (response.status === 200 || response.status === 201) {
            this.snackbar.show = true;
            this.successfulMessage = "Data deleted successfully";
            const i = this.houseownerinfo.map(item => item.id).indexOf(id); // find index of your object
            this.houseownerinfo.splice(i, 1);
          }
          console.log(response);
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>
<style scoped>
