<template>
  <div class="pa-10">
    <v-btn class="primary" :to="{ name: 'application_tab', params: { id: regId } }">Back</v-btn>
    <v-stepper v-model="el" class="vstepper">
      <v-layout justify-center>
        <v-flex xs3 class="mt-3">
          <v-stepper-header class="stepperHeader">
            <template v-for="n in steps" class="stepperHeaderTemplate">
              <v-card :key="n.sn" class="sober elevation-12 pt-1 pb-1">
                <v-stepper-step :key="n.sn" :step="n.sn" editable complete class="vstepperStep">
                  {{ n.text }}
                </v-stepper-step>
              </v-card>
            </template>
          </v-stepper-header>
        </v-flex>
        <v-flex xs10>
          <v-card>
            <v-stepper-items>
              <v-stepper-content v-for="n in steps" :key="`${n.sn}-content`" :step="n.sn" vertical>
                <component
                  :reg="application"
                  :isPostAuthorized="
                    userRoleId == 2 && reg.desk_user_role_id == userRoleId && reg.phase_id == 1
                  "
                  :is="getComponent(n.sn)"
                  :next-step="nextStep2"
                  v-if="n.sn === el"
                  :registration-id="regId"
                  :id="id"
                />
              </v-stepper-content>
            </v-stepper-items>
          </v-card>
        </v-flex>
      </v-layout>
    </v-stepper>
  </div>
</template>
<script>
import Jaggadhani from "./components/Jaggadhani.vue";
import Malpot from "./components/Malpot.vue";
import JaggadhaniPurja from "./components/JaggadhaniPurja.vue";
import Citizenship from "./components/Citizenship.vue";
import Blueprint from "./components/Blueprint.vue";
import DesignerCertificate from "./components/DesignerCertificate.vue";
import Manjurinama from "./components/Manjurinama.vue";
import Warish from "./components/Warish.vue";
import DesignerManjurinama from "./components/DesignerManjurinama.vue";
import Map from "./components/Map.vue";
import Ghardhanikophoto from "./components/Ghardhanikophoto.vue";

const COMPONENT_MAP = {
  1: "Jaggadhani",
  2: "Malpot",
  3: "JaggadhaniPurja",
  4: "Citizenship",
  5: "Blueprint",
  6: "DesignerCertificate",
  7: "Manjurinama",
  8: "Warish",
  9: "DesignerManjurinama",
  10: "Map",
  11: "Ghardhanikophoto"
};
export default {
  name: "DocumentUploadTab",
  props: {
    id: {
      type: [String, Number]
    },
    regId: {
      type: [String, Number]
    },
    sn: {
      type: [String, Number]
    }
  },
  data() {
    return {
      el: Number(this.sn),
      userRoleId: localStorage.getItem("auth_user_role_id"),
      // : this.regId,
      // id: this.id,
      reg: [],
      formData: new FormData(),
      steps: [
        { text: ")    ", sn: 1 },
        { text: ")       ", sn: 2 },
        { text: ") .   ", sn: 3 },
        { text: ")    ", sn: 4 },
        { text: ")  ", sn: 5 },
        { text: ")  ", sn: 6 },
        { text: ")  ", sn: 7 },
        { text: ")   ", sn: 8 },
        { text: ")  ", sn: 9 },
        { text: ")  ", sn: 10 },
        { text: ")  ", sn: 11 }
      ]
      // el: 1,
      // steps: 10,
    };
  },
  components: {
    Jaggadhani,
    Malpot,
    JaggadhaniPurja,
    Citizenship,
    Blueprint,
    DesignerCertificate,
    Manjurinama,
    Warish,
    DesignerManjurinama,
    Map,
    Ghardhanikophoto
  },

  watch: {
    steps(val) {
      if (this.el > val) {
        alert(val);
        this.el = val;
      }
    }
  },
  mounted() {
    const urlApp = `api/registration/${this.$route.params.regId}`;
    this.$axios.get(urlApp).then(response => {
      this.reg = response.data.data;
    });
    console.log(this.sn);
    if (this.sn === undefined) {
      this.sn = 1;
    }
  },
  methods: {
    getComponent(n) {
      if (n === null || n === undefined) {
        n = 1;
      }
      return COMPONENT_MAP[n];
    },
    nextStep2(n) {
      if (n === this.steps.sn) {
        this.el = 1;
      } else {
        this.el = n + 1;
      }
    }
  }
};
</script>

