                  color="primary"
                  v-if="isPostAuthorized"
                  v-on:click="deleterecord(charkillaList.id)"
                  >Delete</v-btn
                >
                <v-btn small color="primary" v-else v-on:click="edit(charkillaList.id)">View</v-btn>
