<script>
import { VueAxios } from "@/plugins/axios";

export default {
  name: "Charkilla",
  props: {
    nextStep: {
      type: Function
    },
    registrationId: {
      type: [String, Number]
    },
    id: {
      type: [String, Number]
    },
    application: {
      type: Object
    },
    isPostAuthorized: {
      type: Boolean
    }
  },

  data() {
    return {
      valid: false,
      required(propertyType) {
        return v => (v && v.length > 0) || `${propertyType} is required`;
      },
      requiredDropdown(propertyType) {
        return v => v || `${propertyType} is required`;
      },
      landscapeType: [],
      userRoleId: localStorage.getItem("auth_user_role_id"),
      date: "",
      successfulMessage: "",
      snackbar: {
        show: false,
        message: this.sucessfulMessage,
        color: "success"
      },
      directions: [
        { id: "n", value: "" },
        { id: "e", value: "" },
        { id: "n", value: "" },
        { id: "s", value: "" },
        { id: "w", value: "" }
      ],
      directionsThrough: [
        { id: "front", value: "Front" },
        { id: "left", value: "Left" },
        { id: "right", value: "Right" },
        { id: "back", value: "Back" }
      ],
      charkilla: [],
      newCharkilla: {
        id: 0,
        reg_id: this.registrationId,
        direction: "",
        direction_through: "",
        landscape_type_id: "",
        road_width: "",
        name: "",
        plot_no: "",
        actual_setback: "",
        standard_setback: ""
      },
      isValid: true,
      directionValidationMessage: false,
      directionThroughValidationMessage: false,
      landscapeTypeValidationMessage: false
    };
  },
  //         created() {
  // this.getRole();
  //         },
  mounted() {
    const landscapeTypeUrl = "api/landscape-type";
    VueAxios.get(landscapeTypeUrl)
      .then(response => {
        this.landscapeType = response.data;
      })
      .catch(error => {
        console.log(`api error:${error}`);
      });
    const directionUrl = "api/landscape-type";
    VueAxios.get(directionUrl)
      .then(response => {
        this.landscapeType = response.data;
      })
      .catch(error => {
        console.log(`api error:${error}`);
      });
    const url = `api/registration/${this.registrationId}/charkilla`;
    VueAxios.get(url)
      .then(response => {
        // debugger;
        this.charkilla = response.data.data;
        // this.newCharkilla.clear();
      })
      .catch(error => {
        console.log(`api error:${error}`);
      });

    console.log(this.application);
  },
  methods: {
    //                getRole() {
    //  const urlApp = `api/registration/${this.$route.params.id}`;
    //                  VueAxios.get(urlApp, {
    //                          headers: {
    //                              'X-Requested-With': 'XMLHttpRequest',
    //                              Authorization: `Bearer ${window.localStorage.getItem('token')}`,
    //                          },
    //                      }, {
    //                          timeout: 100000,
    //                      })
    //                      .then((response) => {
    //                        // debugger;
    //                              this.application = response.data.data;

    //                              // console.log(this.userRoleId);
    //                              // console.log(this.disabled);
    //                      });
    //             },
    save() {
      // debugger;
      if (
        this.newCharkilla.id === "null" ||
        this.newCharkilla.id === 0 ||
        this.newCharkilla.id === undefined
      ) {
        const saveUrl = `/api/registration/${this.registrationId}/charkilla`;
        VueAxios.post(saveUrl, this.newCharkilla)
          .then(response => {
            if (response.status === 200 || response.status === 201) {
              this.charkilla.push(response.data.data);
              this.snackbar.show = true;
              this.successfulMessage = "Successful.";
              this.$refs.form.reset();
            }
          })
          .catch(error => {
            // alert(2);
            console.log(`api error:${error}`);
          });
      } else {
        const eurl = `/api/registration/${this.$route.params.regId}/charkilla/${this.newCharkilla.id}`;
        VueAxios.put(
          eurl,
          this.newCharkilla,
          {
            headers: {
              "X-Requested-With": "XMLHttpRequest",
              Authorization: `Bearer ${window.localStorage.getItem("token")}`
            },
            _method: "patch"
          },
          {}
        )
          .then(response => {
            // debugger;
            if (response.status === 200 || response.status === 201) {
              const i = this.charkilla.map(item => item.id).indexOf(this.newCharkilla.id); // find index of your object
              this.charkilla[i].direction = this.newCharkilla.direction;
              this.charkilla[i].direction_through = this.newCharkilla.direction_through;
              this.charkilla[i].phonelandscape_type_id_no = this.newCharkilla.phone_no;
              this.charkilla[i].road_width = this.newCharkilla.road_width;
              this.charkilla[i].name = this.newCharkilla.name;
              this.charkilla[i].plot_no = this.newCharkilla.plot_no;
              this.charkilla[i].standard_setback = this.newCharkilla.standard_setback;
              this.charkilla[i].plot_no = this.newCharkilla.plot_no;
              this.snackbar.show = true;
              this.successfulMessage = "Data updated successfully";
              this.$refs.form.reset();
            }
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    edit(id) {
      //   debugger;
      //    const url = `api/registration/${this.$route.params.regId}/charkilla/${this.$route.params.id}`;
      const url = `api/registration/${this.$route.params.regId}/charkilla/${id}`;
      VueAxios.get(url).then(response => {
        // debugger;
        this.newCharkilla = response.data.data;
        // console.log(response.data.data);
      });
    },
    deleterecord(id) {
      const deleteurl = `api/registration/${this.$route.params.regId}/charkilla/${id}`;
      VueAxios.delete(deleteurl)
        .then(response => {
          if (response.status === 200 || response.status === 201) {
            this.snackbar.show = true;
            this.successfulMessage = "Data deleted successfully";
            const i = this.charkilla.map(item => item.id).indexOf(id); // find index of your object
            this.designer.splice(i, 1);
          }
          console.log(response);
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>

