<template>
  <div class="pa-10">
    <v-btn class="primary" :to="{ name: 'application_tab', params: { id: regId } }">Back</v-btn>
    <v-stepper v-model="el" class="vstepper">
      <v-layout justify-center>
        <v-flex xs3>
          <v-stepper-header class="stepperHeader">
            <template v-for="n in steps" class="stepperHeaderTemplate">
              <v-card :key="n.sn" class="sober elevation-12 pt-1 pb-1">
                <v-stepper-step :key="n.sn" :step="n.sn" editable complete class="vstepperStep">
                  {{ n.text }}
                </v-stepper-step>
              </v-card>
            </template>
          </v-stepper-header>
        </v-flex>
        <v-flex xs10>
          <v-card>
            <v-stepper-items>
              <v-stepper-content v-for="n in steps" :key="`${n.sn}-content`" :step="n.sn" vertical>
                <component
                  class="sober"
                  :application="application"
                  :isPostAuthorized="
                    userRoleId == 2 && reg.desk_user_role_id == userRoleId && reg.phase_id == 1
                  "
                  :is="getComponent(n.sn)"
                  v-if="n.sn === el"
                  v-bind:next-step="nextStep"
                  v-bind:registration-id="regId"
                  :id="id"
                />
              </v-stepper-content>
            </v-stepper-items>
          </v-card>
        </v-flex>
      </v-layout>
    </v-stepper>
  </div>
</template>
<script>
import Application from "./components/Application.vue";
import Houseownerinfo from "./components/Houseownerinfo.vue";
import Designer from "./components/Designer.vue";
import Supervisor from "./components/Supervisor.vue";
import Landinfoaddress from "./components/Landinfoaddress.vue";
import Charkilla from "./components/Charkilla.vue";
import Landinfoarea from "./components/Landinfoarea.vue";
import Landowner from "./components/Landowner.vue";
import Contractor from "./components/Contractor.vue";
import Applicant from "./components/Applicant.vue";
// import Mason from './components/Mason.vue';
// import Consultant from './components/Consultant.vue';

const COMPONENT_MAP = {
  1: "Application",
  2: "Houseownerinfo",
  3: "Designer",
  4: "Supervisor",
  5: "Landinfoaddress",
  6: "Charkilla",
  7: "Landinfoarea",
  8: "Landowner",
  9: "Contractor",
  10: "Applicant"
  // 11: 'Mason',
  // 12: 'Consultant',
};

export default {
  name: "TabbedElements",
  props: {
    id: {
      cast: Number
    },
    regId: {
      cast: Number
    },
    sn: {
      cast: Number
    }
  },
  data() {
    return {
      el: Number(this.sn),
      userRoleId: localStorage.getItem("auth_user_role_id"),
      vertical: true,
      // : this.regId,
      // id: this.id,
      reg: [],
      steps: [
        { text: "", sn: 1 },
        { text: " ", sn: 2 },
        { text: "", sn: 3 },
        { text: "", sn: 4 },
        { text: "  ()", sn: 5 },
        { text: "", sn: 6 },
        { text: "  ()", sn: 7 },
        { text: " ", sn: 8 },
        { text: " / ", sn: 9 },
        { text: " ", sn: 10 }
      ]
      //  { text: '', sn: '11' },
      // { text: '', sn: '12' }],

      // el: 1,
      // steps: 10,
    };
  },
  components: {
    Application,
    Houseownerinfo,
    Designer,
    Supervisor,
    Landinfoaddress,
    Charkilla,
    Landinfoarea,
    Landowner,
    Contractor,
    Applicant
    //  Mason,
    // Consultant,
  },

  watch: {
    steps(val) {
      if (this.el > val) {
        this.el = val;
      }
    }
  },

  methods: {
    getComponent(n) {
      return COMPONENT_MAP[n];
    },
    nextStep(n) {
      // debugger;
      if (n === this.steps.sn) {
        this.el = 1;
      } else {
        this.el = n + 1;
      }
    }
  },
  mounted() {
    const urlApp = `api/registration/${this.$route.params.regId}`;
    this.$axios.get(urlApp).then(response => {
      this.reg = response.data.data;
    });
  }
};
</script>
<style scoped>
.vstepperStep {
  width: 280px;
  padding: 15px;
}
.vstepper {
  min-height: 100vh;
}
.stepperHeaderTemplate .stepperHeader {
  height: 10px;
  width: 300px;
}
</style>
