<template>
  <div>
    <v-card class="mb-12" color="sober" height="auto">
      <v-toolbar color="primary" dark flat>
        <v-toolbar-title>
           / 
        </v-toolbar-title>
      </v-toolbar>
      <v-form ref="form" class="mx-4" v-model="valid">
        <v-row>
          <v-col cols="2">
            Name
          </v-col>
          <v-col cols="4">
            <v-text-field
              v-model="newcontractor.name"
              :rules="[required('Name')]"
              label=""
              required
            ></v-text-field>
          </v-col>
          <v-col cols="2">
            License no
          </v-col>
          <v-col cols="4">
            <v-text-field v-model="newcontractor.license_no" label="" required></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="2">
            Phone
          </v-col>
          <v-col cols="4">
            <v-text-field v-model="newcontractor.phone_no" label="" required></v-text-field>
          </v-col>
        </v-row>
        <v-snackbar
          v-model="snackbar.show"
          :bottom="y === 'bottom'"
          :color="color"
          :left="x === 'left'"
          :multi-line="mode === 'multi-line'"
          :right="x === 'right'"
          :timeout="3000"
          :top="y === 'top'"
          :vertical="mode === 'vertical'"
        >
          {{ successfulMessage }}
        </v-snackbar>
        <v-btn primary v-if="isPostAuthorized" v-on:click="save" class="primary" :disabled="!valid"
          >Submit</v-btn
        >
      </v-form>
      <v-toolbar color="primary mt-2" dark flat>
        <v-toolbar-title>
          List
        </v-toolbar-title>
      </v-toolbar>
      <v-simple-table class="sober">
        <template v-slot:default>
          <thead>
            <tr class="registrationtable">
              <th class="text-left registrationtable">
                Name
              </th>
              <th class="text-left registrationtable">
                Phone
              </th>
              <th class="text-left registrationtable">License No</th>
              <th class="text-left registrationtable">Action</th>
            </tr>
          </thead>
          <tbody>
            <tr
              class="registrationtable"
              v-for="contractorList in contractor"
              :key="contractorList.id"
            >
              <td class="registrationtable">
                {{ contractorList.name }}
              </td>
              <td>{{ contractorList.phone_no }}</td>
              <td class="registrationtable">{{ contractorList.license_no }}</td>
              <td>
                <v-btn
                  small
                  color="primary"
                  v-if="isPostAuthorized"
                  v-on:click="edit(contractorList.id)"
                  >Edit</v-btn
                >
                <v-btn
                  small
                  color="primary"
                  v-if="isPostAuthorized"
                  v-on:click="deleterecord(contractorList.id)"
                  >Delete</v-btn
                >
                <v-btn small color="primary" v-else v-on:click="edit(landownerList.id)">View</v-btn>
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-card>
  </div>
</template>
<script>
import { VueAxios } from "@/plugins/axios";

export default {
  name: "contractor",
  props: {
    nextStep: {
      type: Function
    },
    registrationId: {
      type: [String, Number]
    },
    id: {
      type: [String, Number]
    },
    application: {
      type: Object
    },
    isPostAuthorized: {
      type: Boolean
    }
  },

  data() {
    return {
      valid: false,
      required(propertyType) {
        return v => (v && v.length > 0) || `${propertyType} is required`;
      },
      requiredDropdown(propertyType) {
        return v => v || `${propertyType} is required`;
      },
      date: "",
      userRoleId: localStorage.getItem("auth_user_role_id"),
      contractor: [],
      successfulMessage: "",
      snackbar: {
        show: false,
        message: this.sucessfulMessage,
        color: "success"
      },
      newcontractor: {
        id: 0,
        reg_id: this.registrationId,
        name: "",
        license_no: "",
        phone_no: ""
      }
    };
  },
  mounted() {
    // console.log(localStorage.getItem("token"));

    const url = `api/registration/${this.registrationId}/contractor`;
    VueAxios.get(url)
      .then(response => {
        // debugger;
        this.contractor = response.data.data;
        // this.newcontractor.clear();
      })
      .catch(error => {
        console.log(`api error:${error}`);
      });
  },
  methods: {
    clearAllData() {
      this.newcontractor.id = 0;
      this.newcontractor.reg_id = this.registrationId;
      this.newcontractor.name = "";
      this.newcontractor.license_no = "";
      this.newcontractor.phone_no = "";
    },
    save() {
      //   debugger;
      if (
        this.newcontractor.id === "null" ||
        this.newcontractor.id === 0 ||
        this.newcontractor.id === undefined
      ) {
        const saveUrl = `/api/registration/${this.registrationId}/contractor`;
        VueAxios.post(saveUrl, this.newcontractor)
          .then(response => {
            if (response.status === 200 || response.status === 201) {
              this.contractor.push(response.data.data);
              this.snackbar.show = true;
              this.successfulMessage = "Record added Successfuly.";
              this.$refs.form.reset();
            }
          })
          .catch(error => {
            // alert(2);
            console.log(`api error:${error}`);
          });
      } else {
        const eurl = `/api/registration/${this.$route.params.regId}/contractor/${this.newcontractor.id}`;
        VueAxios.put(
          eurl,
          this.newcontractor,
          {
            headers: {
              "X-Requested-With": "XMLHttpRequest",
              Authorization: `Bearer ${window.localStorage.getItem("token")}`
            },
            _method: "patch"
          },
          {}
        )
          .then(response => {
            // debugger;
            if (response.status === 200 || response.status === 201) {
              const i = this.contractor.map(item => item.id).indexOf(this.newcontractor.id); // find index of your object
              this.contractor[i].name = this.newcontractor.name;
              this.contractor[i].license_no = this.newcontractor.license_no;
              this.contractor[i].phone_no = this.newcontractor.phone_no;
              this.snackbar.show = true;
              this.successfulMessage = "Data updated successfully";
              this.$refs.form.reset();
            }
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    edit(id) {
      //   debugger;
      //    const url = `api/registration/${this.$route.params.regId}/contractor/${this.$route.params.id}`;
      const url = `api/registration/${this.$route.params.regId}/contractor/${id}`;
      VueAxios.get(url).then(response => {
        // debugger;
        this.newcontractor = response.data.data;
        console.log(response.data.data);
      });
    },
    deleterecord(id) {
      const deleteurl = `api/registration/${this.$route.params.regId}/contractor/${id}`;
      VueAxios.delete(deleteurl)
        .then(response => {
          // debugger;
          if (response.status === 200 || response.status === 201) {
            this.snackbar.show = true;
            this.successfulMessage = "Data deleted successfully";
            const i = this.contractor.map(item => item.id).indexOf(id); // find index of your object
            this.contractor.splice(i, 1);
          }
          console.log(response);
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>

<style scoped>
