<template>
  <div class="vld-parent">
    <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="true"></loading>

    <v-card class="sober">
      <!-- <v-app id="inspire"> -->
      <v-card class="sober d-flex flex-column">
        <v-toolbar color="primary" dark flat>
          <v-toolbar-title>
            
          </v-toolbar-title>
        </v-toolbar>
        <!-- <h2></h2> -->
        <v-form ref="form" class="mx-4" v-model="valid">
          <v-row>
            <v-col md="4">
              <label>  (..)<span>*</span></label>
              <!-- {{id}} -->
            </v-col>
            <v-col cols="4">
              <v-text-field
                v-model="application.application_date_np"
                :rules="[required(' (..)')]"
                label=" (..)"
                class=" "
                clearable
                ref="dateField"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="4">
              <label>  <span>*</span></label>
            </v-col>

            <v-col cols="4">
              <!-- <v-text-field v-model="application.building_use_id" label=""      ></v-text-field> -->

              <v-select
                v-model="application.building_use_id"
                :rules="[requiredDropdown('Building Use')]"
                :items="buildingUse"
                item-value="id"
                item-text="building_use_type"
                :key="id"
                dense
              ></v-select>
            </v-col>
            <v-col cols="4">
              <span class="error-message" style="color: red" v-if="buildingUseValidationMessage"
                >Must select an option</span
              >
            </v-col>
          </v-row>
          <v-row>
            <v-col md="4">
              <label>     <span>*</span></label>
            </v-col>

            <v-col md="4">
              <!-- <v-text-field v-model="application.building_category_id" label=""      ></v-text-field>
                                 -->
              <v-select
                required
                v-model="application.building_category_id"
                :rules="[requiredDropdown('Building Category')]"
                :items="buildingCategory"
                item-value="id"
                item-text="building_category_type"
                :key="id"
                dense
              ></v-select>
            </v-col>
            <v-col cols="4">
              <span
                class="error-message"
                style="color: red"
                v-if="buildingCategoryValidationMessage"
                >Must select an option</span
              >
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" md="4">
              <label>   <span>*</span></label>
            </v-col>

            <v-col md="4">
              <v-select
                required
                v-model="application.building_structure_id"
                :rules="[requiredDropdown('Building Structure')]"
                :items="buildingStructure"
                item-value="id"
                item-text="building_structure_type"
                :key="id"
                dense
              ></v-select>
            </v-col>
            <v-col cols="4">
              <span
                class="error-message"
                style="color: red"
                v-if="buildingStructureValidationMessage"
                >Structure is required</span
              >
            </v-col>
          </v-row>
          <v-row>
            <v-col md="4">
              <label>  <span>*</span></label>
            </v-col>

            <v-col md="2">
              <v-text-field
                required
                v-model="application.building_length_ft"
                :rules="[required('Length (ft)')]"
                label="feet"
                dense
              ></v-text-field>
            </v-col>
            <v-col md="2">
              <v-text-field
                required
                v-model="application.building_length_in"
                :rules="[required('Length (in)')]"
                label="inch"
                dense
              ></v-text-field>
            </v-col>
            <v-col cols="4">
              <span class="error-message" style="color: red" v-if="buildingLengthValidationMessage"
                >Length is required</span
              >
            </v-col>
          </v-row>
          <v-row>
            <v-col md="4">
              <label>  <span>*</span></label>
            </v-col>

            <v-col md="2">
              <v-text-field
                required
                v-model="application.building_breadth_ft"
                :rules="[required('Breadth (ft)')]"
                label="feet"
                dense
              ></v-text-field>
            </v-col>
            <v-col md="2">
              <v-text-field
                required
                v-model="application.building_breadth_in"
                :rules="[required('Breadth(in)')]"
                label="inch"
                dense
              ></v-text-field>
            </v-col>
            <v-col cols="4">
              <span class="error-message" style="color: red" v-if="buildingBreadthValidationMessage"
                >Breadth is required</span
              >
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" md="4">
              <label>   <span>*</span></label>
            </v-col>

            <v-col cols="12" md="2">
              <v-text-field
                required
                v-model="application.building_height_ft"
                :rules="[required('Height (ft)')]"
                label="feet"
                dense
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="2">
              <v-text-field
                required
                v-model="application.building_height_in"
                :rules="[required('Height (in)')]"
                label="inch"
                dense
              ></v-text-field>
            </v-col>
            <v-col cols="4">
              <span class="error-message" style="color: red" v-if="buildingHeightValidationMessage"
                >Height is required</span
              >
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" md="4">
              <label>  <span>*</span></label>
            </v-col>

            <v-col cols="12" md="4">
              <v-text-field
                v-model="application.building_storey"
                :rules="[required('Storey')]"
                label=" "
              ></v-text-field>
            </v-col>
            <v-col cols="4">
              <span class="error-message" style="color: red" v-if="buildingStoreyValidationMessage"
                >Storey is required</span
              >
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" md="4">
              <label> Total Area (Total Building Floor Area)<span>*</span></label>
            </v-col>
            <!-- <v-text-field v-model="application.reg_id" :value="applicationrole.id" type="hidden" ></v-text-field> -->
            <v-col cols="12" md="4">
              <v-text-field
                required
                v-model="application.building_max_area"
                :rules="[required('Total Area')]"
                label="Total Area (sqm)"
                dense
              ></v-text-field>
            </v-col>
            <v-col cols="4">
              <span class="error-message" style="color: red" v-if="buildingAreaValidationMessage"
                >Area is required</span
              >
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-btn
                primary
                v-on:click="save"
                v-if="isPostAuthorized"
                class="primary"
                :disabled="!valid"
                >Submit</v-btn
              >
            </v-col>
          </v-row>
        </v-form>

        <v-snackbar
          v-model="snackbar.show"
          :bottom="y === 'bottom'"
          :color="color"
          :left="x === 'left'"
          :multi-line="mode === 'multi-line'"
          :right="x === 'right'"
          :timeout="3000"
          :top="y === 'top'"
          :vertical="mode === 'vertical'"
        >
          {{ successfulMessage }}
        </v-snackbar>
      </v-card>
      <!-- </v-app> -->
    </v-card>
  </div>
</template>

<script>
import { VueAxios } from "@/plugins/axios";
import * as nepaliDatePicker from "../../../../node_modules/nepali-date-picker/dist/jquery.nepaliDatePicker.min.js"; /*eslint-disable-line*/
import "../../../../node_modules/nepali-date-picker/dist/nepaliDatePicker.min.css";

export default {
  name: "Application",
  props: {
    nextStep: {
      type: Function
    },
    registrationId: {
      type: [String, Number]
    },
    id: {
      type: [String, Number]
    },
    isPostAuthorized: {
      type: Boolean
    }
  },

  data() {
    return {
      valid: false,
      required(propertyType) {
        return v => (v && v.length > 0) || `${propertyType} is required`;
      },
      requiredDropdown(propertyType) {
        return v => v || `${propertyType} is required`;
      },
      date: "",
      isValid: true,
      userRoleId: localStorage.getItem("auth_user_role_id"),
      applicationDateNpValidationMessage: false,
      buildingUseValidationMessage: false,
      buildingCategoryValidationMessage: false,
      buildingStructureValidationMessage: false,
      buildingLengthValidationMessage: false,
      buildingBreadthValidationMessage: false,
      buildingHeightValidationMessage: false,
      buildingStoreyValidationMessage: false,
      buildingAreaValidationMessage: false,
      buildingUse: [],
      buildingCategory: [],
      buildingStructure: [],
      application: {
        id: 0,
        // reg_id: 0,
        application_date: "2020-04-22",
        application_date_np: "",
        building_category_id: "",
        building_use_id: "",
        building_structure_id: "",
        building_length_in: "",
        building_breadth_in: "",
        building_height_in: "",
        building_length_ft: "",
        building_breadth_ft: "",
        building_height_ft: "",
        building_storey: "",
        building_max_area: ""
      },
      applicationrole: [],
      // applicationList: [],
      successfulMessage: "",
      snackbar: {
        show: false,
        message: this.sucessfulMessage,
        color: "success"
      }
    };
  },
  computed: {
    isLoading() {
      if (
        this.buildingUse.length === 0 ||
        this.buildingCategory.length === 0 ||
        this.buildingStructure.length === 0
      ) {
        // all drop-down values not initiated from api
        return true;
      }
      return false;
    }
  },
  created() {
    this.buildingUseCall();
    this.buildingCategoryCall();
    this.buildingStructureCall();
    this.applicationDetailCall();
    this.getRole();
  },
  mounted() {
    const { dateField } = this.$refs;
    if (dateField) {
      $(dateField.$el).nepaliDatePicker({
        dateFormat: "%y-%m-%d",
        closeOnDateSelect: true
      });
      $(dateField.$el).on("dateSelect", event => {
        console.log(`Date: ${event.datePickerData.formattedDate}`);
        this.application.application_date_np = event.datePickerData.formattedDate;
      });
    }
  },
  methods: {
    getRole() {
      const urlApp = `api/registration/${this.$route.params.regId}`;
      VueAxios.get(urlApp, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`
        }
      }).then(response => {
        //    debugger;
        this.applicationrole = response.data.data;
        console.log(this.applicationrole.phase_id);
        console.log(this.userRoleId);
        console.log(this.applicationrole.desk_user_role_id);
      });
    },
    applicationDetailCall() {
      const url = `api/registration/${this.registrationId}/application`;
      VueAxios.get(url, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`
        }
      })
        .then(response => {
          // debugger;
          this.application = response.data.data;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    buildingUseCall() {
      const buildingUseUrl = "api/building-use";
      VueAxios.get(buildingUseUrl)
        .then(response => {
          this.buildingUse = response.data;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    buildingCategoryCall() {
      const buildingCategoryUrl = "api/building-category";
      VueAxios.get(buildingCategoryUrl)
        .then(response => {
          this.buildingCategory = response.data;
          // console.log(this.buildingCategory);
          // debugger;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    buildingStructureCall() {
      const buildingStructureUrl = "api/building-structure";
      VueAxios.get(buildingStructureUrl, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`
        }
      })
        .then(response => {
          this.buildingStructure = response.data;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    save() {
      if (
        this.application.id === null ||
        this.application.id === 0 ||
        this.application.id === undefined
      ) {
        const url = `/api/registration/${this.$route.params.regId}/application`;
        VueAxios.post(
          url,
          this.application,
          {
            headers: {
              "X-Requested-With": "XMLHttpRequest",
              Authorization: `Bearer ${window.localStorage.getItem("token")}`
            }
          },
          {
            timeout: 10000
          }
        )
          .then(response => {
            // debugger;
            if (response.status === 200 || response.status === 201) {
              this.application = response.data.data;
              this.snackbar.show = true;
              this.successfulMessage = "Successful.";
            }
          })
          .catch(error => {
            // alert(2);
            console.log(`api error:${error}`);
          });
      } else {
        const eurl = `/api/registration/${this.$route.params.regId}/application/${this.application.id}`;
        VueAxios.put(
          eurl,
          this.application,
          {
            headers: {
              "X-Requested-With": "XMLHttpRequest",
              Authorization: `Bearer ${window.localStorage.getItem("token")}`
            },
            _method: "patch"
          },
          {}
        )
          .then(response => {
            // debugger;
            if (response.status === 200 || response.status === 201) {
              this.snackbar.show = true;
              this.successfulMessage = "Data Updated successfully";
            }
            // else {
            //     this.$router.push({
            //         name: 'tabbed',
            //     });
            // }
            console.log(response);
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    edit(id) {
      // debugger;
      //    const url = `api/registration/${this.$route.params.regId}/application/${this.$route.params.id}`;
      const url = `api/registration/${this.$route.params.regId}/application/${id}`;
      VueAxios.get(url).then(response => {
        // debugger;
        this.application = response.data.data;
        console.log(response.data.data);
      });
    },
    deleterecord(id) {
      const deleteurl = `api/registration/${this.$route.params.regId}/application/${id}`;
      VueAxios.delete(deleteurl, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`
        }
      })
        .then(response => {
          // debugger;
          if (response.status === 200 || response.status === 201) {
            this.snackbar.show = true;
            this.successfulMessage = "Data deleted successfully";
          }
          console.log(response);
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>

